@charset "UTF-8";

.comn-loading {
	position: fixed;
	top: 50%;
	left: 50%;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: rgba(0,0,0,0.6);
	transform: translate3d(-50%, -50%, 0);
	z-index: 100;
	.text {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -120%);
		width:129px;
		height:27px;
		background-repeat:no-repeat;
		background-position: 0 0;
		background-size: 100%;
		img {
			width:129px;
		}
	}
	.icon {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, 60%);
		display: block;
		width:25px;
		height:25px;
		animation: loadingPlay 0.6s steps(21) infinite;
		background:url('//img.modetour.com/ModeMobile/2019/common/img/img_loading_icon_move.png') no-repeat 0 0;
		background-size: 525px;
		@keyframes loadingPlay {
			100% { background-position: -525px; }
		}
	}
	.loading2020 {
		position: absolute;
		left: 0;
		top: 50%;
		width:100%;
		padding-top: 35px;
		padding-bottom: 35px;
		transform: translate(0, -50%);		
		color:#fff;
		text-align: center;
		&:before {
			position: absolute;
			left: 50%;
			top:0;
			display: inline-block;
			width:129px;
			height:27px;
			margin-left:-64.5px;
			content: '';
			background: url(//img.modetour.com/ModeMobile/2019/common/img/img_loading_txt.png) no-repeat 0 0;
			background-size:129px;
		}
		&:after {
			position: absolute;
			left: 50%;
			bottom:0;
			display: inline-block;
			width:22px;
			height:22px;
			margin-left:-11px;
			content: '';
			background: url(//img.modetour.com/ModeMobile/2019/common/img/img_loading_icon.gif) no-repeat 0 0;
			background-size: 22px;
		}
	}
}