@charset "UTF-8";

.pop-layer {
	display: none;
	position: absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	z-index: 1000;
	-webkit-overflow-scrolling: auto;
	.screen {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: rgba(0,0,0,.7);
	}
}

