@charset "UTF-8";

.search-integrate {
	background-color: #fff;
	.pop-con {
		display: block;
		width:100%;
		height: 100%;
		overflow: hidden;
		overflow-y:auto;
		-webkit-overflow-scrolling: touch;
		word-break:break-all;
	}
	.srch-move {
		position: relative;
		height: 58px;
		padding:9px 23px 9px 50px;
		background-color:#fafafa;
		border-bottom: 1px solid #e6e6e6;
		box-sizing: border-box;
		.back {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width:50px;
			height:58px;
			line-height:58px;
			padding-left:23px;
			&:after {
				@include btn_srch_prev();
				content:'';
			}
		}
		.ipt-srch {
			width:100%;
			height:40px;
			line-height:20px;
			padding:10px 24px;
			background-color: #fff;
			border-radius:20px;
			border:none;
			font-size: calculateRem(15px);
			color: #333;
			&::placeholder {
				color: #bebebe;
			}
		}
		.btn-srch {
			position: absolute;
			right:24px;
			top:0;
			display: block;
			width:37px;
			line-height:45px;
			height:40px;
			margin:9px 0;
			border: none;
			background: none;
			&:after {
				@include btn_srch_mag();
				position: absolute;
				right:15px;
				top:11px;
				content:'';
			}
		}
		.btn-erase {
			position: absolute;
			right:61px;
			top:0;
			display: block;
			width:27px;
			line-height:45px;
			height:40px;
			margin:9px 0;
			border: none;
			background: none;
			&:after {
				@include btn_srch_erase();
				position: absolute;
				right:5px;
				top:11px;
				content:'';
			}
		}
	}
	.item-tab {
		position: relative;
		height:43px;
		// line-height:49px;
		padding:0 23px;
		overflow: hidden;
		&:before {
			position: absolute;
			left:0;
			bottom:0;
			content:'';
			display: block;
			width:100%;
			height: 1px;
			background:#e6e6e6;
		}
		li {
			float:left;
		}
		a {
			position: relative;
			display: block;
			height: 100%;
			height:43px;
			padding-top:14px;
			padding-left: 10px;
			padding-right: 10px;
			color:#333;
			font-size: calculateRem(14px);
			span {
				color:#bfbfbf;
			}
			&.current {
				font-weight: bolder;
				span {
					color:#43c866;
				}
				&:after {
					position: absolute;
					left:10px;
					top:42px;
					content:'';
					display: block;
					width:calc(100% - 20px);
					height: 1px;
					background:#333;
				}
			}
		}
	}
	.ui-tabs-contents-wrap {
		.ui-tab-content:nth-child(3) {
			.range-area{
				.pop-popular {					
					.popular-wrap {
						right:23px;
					}
				}
			} 
		}
	}
	.range-area {
		position: relative;
		height: 39px;
		padding:0 23px 0;
		font-size: calculateRem(14px);
		color:#979797;
		&:before {
			position: absolute;
			left:0;
			bottom:0;
			content:'';
			display: block;
			width:100%;
			height: 1px;
			background:#e6e6e6;
		}
		.num {
			float:left;
			height:100%;
			padding-top:11px;
		}
		.calendar {
			float:left;
			margin-left:15px;
			padding-top:12px;
			padding-bottom:5px;
			&:before {
				@include ico_srch_calendar();
				content:'';
				margin-right:4px;
			}
		}
		.sec-sort {
			float: right;
			> a {
				position: relative;
				display:inline-block;
				height:100%;
				// line-height:calculateRem(17px);
				vertical-align: top;
			}
			.btn-change {
				width:30px;
				padding:12px 8px 5px 8px;
				&:before { //bar
					position: absolute;
					right:0;
					top:50%;
					display: block;
					width:1px;
					height:12px;
					margin-top:-2px;
					background-color:#d2d2d2;
					content:'';
				}
				&:after {
					@include btn_srch_gall();
					content:'';
				}
				&.thum {
					&:after {
						@include btn_srch_list();
						content:'';
					}
				}
			}
			.btn-popular {
				padding:12px 6px 5px 3px;
				z-index: 3;
				&:after {
					@include ico_srch_arr();
					content:'';
					margin-left: 3px;
					vertical-align: middle;
				}
				&.is-open {
					&:after {
						transform: rotate(180deg);
					}
					~ .pop-popular {
						display: block;
					}
				}
			}
			.btn-filter {
				padding:0 5px 0 5px;
				margin-top:12px;
				background-color: #979797;
				color:#fff;	
				border-radius: 2px;
				z-index: 3;
				&:before {
					@include ico_srch_filter_on();
					content:'';
					margin-right: 5px;
					margin-top: -3px;
					vertical-align: middle;
				}
				&.is-open {
					background-color: #43c866;
					~ .pop-filter {
						display: block;
					}
				}
			}
		}
		//순서정렬 팝업
		.pop-popular {
			position: absolute;
			left:0;
			top:0;
			display: none;
			width: 100%;
			height:100vh;
			z-index: 2;
			.screen {
				display: block;
				width: 100%;
				height:100vh;
				margin-top:-101px;
				background:transparent;
			}
			.popular-wrap {
				position: absolute;
				top:39px;
				right:62px;
				width: calculateRem(100px);
				padding:15px 10px 15px 0;
				background-color: #fff;
				box-shadow: 1px 1px 10px rgba(212,212,212,.5);
				overflow: hidden;
				a {
					display: block;
					margin: 0 0 2px;
					text-align: right;
					font-weight: 300;
					font-size: calculateRem(14px);
					color:#979797;
					&.on {
						font-weight: normal;
					}
				}
			}
		}
		//필터 팝업
		.pop-filter {
			position: absolute;
			left:0;
			top:39px;
			display: none;
			width: 100%;
			height:100vh;
			z-index: 2;
			.screen {
				display: block;
				width: 100%;
				height: 100%;
				padding:6px 23px 0;
				background:rgba(0,0,0,.6);
			}
			.filter-wrap {
				position: absolute;
				left:0;
				top:0;
				display: block;
				width: 100%;
				padding:0 23px 0;
				background-color: #fff;
				box-shadow: 1px 1px 10px rgba(212,212,212,.2);
			}
		}
		.option-tab {
			// margin-left: -10px;
			border-bottom: 1px solid #e6e6e6;
			overflow: hidden;	
			li {
				float:left;
				// margin-right: 20px;
				&:first-child {
					a {
						padding-left:0;
					}
				}
			}
			a {
				position: relative;
				display: block;
				height:40px;
				padding-top:12px;
				padding-left:10px;
				padding-right:10px;
				color:#979797;
				font-size: calculateRem(14px);
				&.current {
					color:#333;
					font-weight: bolder;
				}
			}
		}
		.option-detail {
			margin:0 0 3px;
			padding:15px 0 0;
		}
		.chk-wrap {
			position: relative;
			display: inline-block;
			margin:0 10px 7px 0;
			.label {
				padding:0 0 0 20px;
				color:#333;
				&:before {
					@include ico_srch_chk_off();
					position: absolute;
					left:0;
					top:50%;
					margin-top:-7px;
					content:'';
					padding:0 0 0 16px;
				}
			}
			input:checked + .label {
				&:before {
					@include ico_srch_chk_on();
				}
	
			}
		}
		.word-area {
			margin:0 0 10px;
			.btn-remove {
				display: inline-block;
				color:#44c866;
				margin:0 10px 7px 0;
				&:after {
					@include btn_srch_remove2();
					content:'';
					margin-left: 5px;
					margin-top:-2px;
					vertical-align: middle;
				}
			}
		}
		.btn-area {
			margin:10px 0 19px;
			text-align: center;
			button {
				display: inline-block;
				min-width: 32%;
				height:  calculateRem(32px);
				line-height: calculateRem(32px);
				border-radius: 2px;
				color:#fff;
				background:#43c866;
				font-size: calculateRem(14px);
			}
		}
	}
	//검색 결과영역
	.srch-result {
		position: relative;
		min-height:calc(100vh - 101px);
		padding:10px 23px 13px 23px;
		background-color: #f8f8f8;
	}
	//선택한 검색어
	.srch-result {
		.word-area {
			margin:0 0 10px;
			background-color: #f8f8f8;
			.btn-remove {
				display: inline-block;
				height: calculateRem(21px);
				line-height: calculateRem(20px);
				padding:0 8px 0 8px;
				background-color:#44c866;
				color:#fff;
				border-radius: 10px;
				font-size: calculateRem(13px);
				&:after {
					@include btn_srch_remove3();
					content:'';
					margin-left: 6px;
					margin-top:-2px;
					vertical-align: middle;
				}
			}
		}
	}
	.lst-pack {
		position: relative;
		overflow: hidden;
		min-height: 129px;
		&:before {
			position: absolute;
			left: 50%;
			top: 40px;
			display: inline-block;
			width: 138px;
			height: 70px;
			margin-left: -69px;
			overflow: hidden;
			background-image: url(//img.modetour.com/ModeMobile/2019/common/bg/bg_noitem.png);
			background-position: 0 0;
			background-size: 138px 70px;
			content: '';
		}
	}
	//리스트형
	.lst-pack.view-list {
		li {
			position: relative;
			z-index: 1;
			min-height: 129px;
			padding:21px 23px 23px;
			margin:0 0 10px;
			border-radius: 2px;
			background-color: #fff;
			overflow: hidden;
		}
		.img {
			display: none;
		}
		.code {
			margin:0 0 4px;
			color:#979797;
			font-size: calculateRem(12px);
		}
		.title {
			margin:0 0 18px;
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			text-overflow: ellipsis;
			word-wrap: break-word;
			font-size: calculateRem(13px);
		}	
		.price {
			display: none;
		}
		.info {
			position: relative;
			overflow:hidden;
			.icons {
				width:131px;
				height:26px;
				overflow: hidden;
				> .tag {
					display: inline-block;
					width:63px;
					height:26px;
					line-height:24px;
					font-size: calculateRem(12px);
					color:#747474;
					border:1px solid #b7b7b7;
					border-radius: 2px;
					vertical-align: top;
					text-align:center;
				}
				.departure {			
					border-color:#b7b7b7;
					color:#747474;
				}
				.pack {
					border-color:#36ce6a;
					color:#36ce6a;
				}
				.self {
					border-color:#2fb8c1;
					color:#2fb8c1;
				}
				.honey {
					border-color:#f8b094;
					color:#f8b094;
				}
				.primium {
					border-color:#f8b094;
					color:#f8b094;
				}
				.golf {
					border-color:#ffc53a;
					color:#ffc53a;
				}
				.cruise {
					border-color:#9f8bec;
					color:#9f8bec;
				}
			}
			.price2 {
				position: absolute;
				right:0;
				top:0;
				height: 26px;
				line-height: 24px;
				text-align: right;
				font-weight: bolder;
				letter-spacing: -0.05em;
				font-size: calculateRem(15px);
			}
		}
	}
	//섬네일형
	.lst-pack.view-thumb {
		li {
			position: relative;
			z-index: 1;
			float:left;
			width:calc(50% - 4px);
			height:269px;
			padding:23px 23px 23px;
			margin:0 0 15px;
			background-color: #fff;
			&:before { //뒷글자 가림
				position: absolute;
				right:-100%;
				top:0;
				display: block;
				width: 100%;
				height: 100%;
				background-color:#f7f7f7;
				content: '';
			}
			&:nth-child(odd) {
				margin-right:4px;
			}
			&:nth-child(even) {
				margin-left:4px;
			}
			a {
				display:block;
			}
		}
		.img {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width:100%;
			height:100%;
			background-size:cover;
			background-position: center center;
			background-repeat:no-repeat;
			border-radius: 2px;
			overflow: hidden;
			&:after {
				position: absolute;
				left:0;
				top:0;
				display: block;
				width:100%;
				height:100%;
				content:'';
				border-radius:0 0 2px 2px;
				overflow: hidden;
				background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0) 30%,rgba(0,0,0,0.65) 100%);
			}
		}
		.code {
			position: absolute;
			right:7px;
			top:7px;
			display: block;
			height:19px;
			line-height:19px;
			padding:0 6px;
			color:#fff;
			font-size: calculateRem(11px);
			background:rgba(0,0,0,.6);
			border-radius: 2px;
		}
		.desc {
			position: absolute;
			left:0;
			bottom:45px;
			display: block;
			padding:0 15px;
			.price {
				color:#fff;
				margin:0 0 6px;
				font-size: calculateRem(15px);
				font-weight: bolder;
			}
			.title {
				// margin:0 0 13px;
				overflow: hidden;
				display: -webkit-box;
				-webkit-line-clamp: 3;
				-webkit-box-orient: vertical;
				text-overflow: ellipsis;
				word-wrap: break-word;
				color:#fff;
				font-size: calculateRem(13px);
			}
		}
		.info {
			color:#fff;
			.icons {
				position: absolute;
				left:16px;
				bottom:14px;
				display: block;
				height:20px;
				overflow: hidden;
				> .tag {
					display: inline-block;
					width:51px;
					height:20px;
					margin-right: 1px;
					line-height:18px;
					font-size: calculateRem(11px);
					color:#fff;
					border:1px solid #b7b7b7;
					border-radius: 2px;
					vertical-align: top;
					text-align:center;
				}
				.departure {			
					border-color:#b7b7b7;
				}
				.pack {
					border-color:#36ce6a;
					background-color:#36ce6a;
				}
				.self {
					border-color:#2fb8c1;
					background-color:#2fb8c1;
				}
				.honey {
					border-color:#f8b094;
					background-color:#f8b094;
				}
				.primium {
					border-color:#f8b094;
					background-color:#f8b094;
				}
				.golf {
					border-color:#ffc53a;
					background-color:#ffc53a;
				}
				.cruise {
					border-color:#9f8bec;
					background-color:#9f8bec;
				}
			}
			.price2 {
				display: none;
			}
		}
	
	}
	//2.기획상품
	.lst-special {
		position: relative;
		min-height:80vh;
		background-color:#f8f8f8;
		padding:6px 0 0;
		&:before {
			position: absolute;
			left: 50%;
			top: 40px;
			display: inline-block;
			width: 138px;
			height: 70px;
			margin-left: -69px;
			overflow: hidden;
			background-image: url(//img.modetour.com/ModeMobile/2019/common/bg/bg_noitem.png);
			background-position: 0 0;
			background-size: 138px 70px;
			content: '';
		}
		li {
			position: relative;
			z-index: 1;
			min-height: 129px;
			margin:0 0 25px;
			border-radius: 2px;
			background-color: #fff;
			border-radius: 4px;
			overflow: hidden;
		}
		.img {
			// display: none;
		}
		.desc {
			padding:17px 18px 18px;
		}
		.title {
			// margin:0 0 5px;
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			text-overflow: ellipsis;
			word-wrap: break-word;
			font-size: calculateRem(14px);
			color:#333;
			font-weight: bolder;
		}
		.period {
			font-size: calculateRem(13px);
			color:#979797;
		}
	}
	//3.할인항공권
	.lst-discount {
		position: relative;
		&:before {
			position: absolute;
			left: 50%;
			top: 40px;
			display: inline-block;
			width: 138px;
			height: 70px;
			margin-left: -69px;
			overflow: hidden;
			background-image: url(//img.modetour.com/ModeMobile/2019/common/bg/bg_noitem.png);
			background-position: 0 0;
			background-size: 138px 70px;
			content: '';
		}
		li {
			position: relative;
			z-index: 1;
			min-height: 155px;
			padding:18px 20px 17px;
			margin:0 0 10px;
			border-radius: 2px;
			background-color: #fff;
		}
		a {
			color:#333;
		}
		.air {
			margin:0 0 2px;
			font-size: calculateRem(12px);
			img {
				width:20px;
				margin-top:-4px;
				vertical-align: middle;
			}
		}
		.title {
			margin:0 0 2px;
			font-size: calculateRem(14px);
			font-weight:bolder;
		}
		.date {
			margin:0 0 22px;
			font-size: calculateRem(13px);
		}
		.info {
			overflow:hidden;
			.tag {
				float: left;
				min-width:52px;
				height:22px;
				padding:0 6px;
				line-height:20px;
				font-size: calculateRem(11px);
				color:#747474;
				border:1px solid #aaa;
				border-radius: 2px;
				vertical-align: top;
				text-align:center;
			}
			.price {
				float: right;
				font-size: calculateRem(15px);
				font-weight:bolder;
			}
		}
		.txt-tax {
			margin-top:-1px;
			text-align: right;
			color:#979797;
			font-size: calculateRem(11px);
		}
	}
}

	