@charset "UTF-8";

.search-detail {
	background-color: #fff;
	a {
		text-decoration: none;
	}
	.pop-tit {
		.close {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width: 50px;
			height: 58px;
			z-index: 1;
		}
	}
	.pop-con {
		display: block;
		width:100%;
		height:100%;
		overflow: hidden;
		overflow-y:auto;
		-webkit-overflow-scrolling: touch;
		word-break:break-all;
	}
	.srch-move {
		position: relative;
		height: 58px;
		padding:9px 23px 9px 50px;
		background-color:#fafafa;
		box-sizing: border-box;
		&:before {
			position: absolute;
			left:0;
			bottom:0;
			content:'';
			display: block;
			width:100%;
			height: 2px;
			background:linear-gradient(140deg, #43c866 0%, #43c866 40%, #2fb8c1 60%, #2fb8c1 100%);
		}
		.back {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width:50px;
			height:58px;
			line-height:58px;
			padding-left:23px;
			&:after {
				@include btn_srch_prev();
				content:'';
			}
		}
		.ipt-srch {
			width:100%;
			height:40px;
			line-height:20px;
			padding:10px 24px;
			background-color: #fff;
			border-radius:20px;
			border:none;
			font-size: calculateRem(15px);
			color: #333;
			font-family: "Noto Sans KR", Arial, sans-serif;
			&::placeholder {
				color: #bebebe;
			}
		}
		.btn-srch {
			position: absolute;
			right:24px;
			top:0;
			display: block;
			width:37px;
			line-height:45px;
			height:40px;
			margin:9px 0;
			border:none;
			background:none;
			&:after {
				@include btn_srch_mag();
				position: absolute;
				right:15px;
				top:11px;
				content:'';
			}		
		}
		.btn-erase {
			position: absolute;
			right:61px;
			top:0;
			display: block;
			width:27px;
			line-height:45px;
			height:40px;
			margin:9px 0;
			border:none;
			background:none;
			&:after {
				@include btn_srch_erase();
				position: absolute;
				right:5px;
				top:11px;
				content:'';
			}
		}
	}
	.item-tab {
		position: relative;
		height:51px;
		line-height:62px;
		padding:0 23px;
		text-align: center;
		overflow: hidden;
		&:before {
			position: absolute;
			left:0;
			bottom:0;
			content:'';
			display: block;
			width:100%;
			height:1px;
			background:#e6e6e6;
		}
		li {
			display: inline-block;
			margin:0 23px;
			vertical-align: top;
		}
		a {
			position: relative;
			display: block;
			height: 100%;
			color:#333;
			font-weight: bolder;
			font-size: calculateRem(14px);
			span {
				color:#bfbfbf;
			}
			&.current {
				span {
					color:#43c866;
				}
				&:after {
					position: absolute;
					left:0;
					top:50px;
					content:'';
					display: block;
					width:100%;
					height: 1px;
					background:#43c866;
				}
			}
		}
	}
	.lst-recent {
		position: relative;
		min-height: calculateRem(40px);
		padding:8px 23px;
		&:before {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width: 100%;
			padding-top:18px;
			font-size: calculateRem(14px);
			text-align: center;
			color:#959595;
			content:'최근 검색어가 없습니다.';
		}
		li {
			position: relative;
			height: calculateRem(35px);
			line-height: calculateRem(35px);
			background-color: #fff;
			overflow:hidden;
			position: relative;
		}
		a {
			display: block;
			font-size: calculateRem(14px);
			color:#333;
			overflow: hidden;
		}
		.word {
			float:left;
		}
		.btn-remove {
			position: absolute;
			right:-12px;
			top:0;
			display: block;			
			width: calculateRem(35px);			
			height: calculateRem(35px);
			&:after {
				@include btn_srch_remove();
				position: absolute;
				right:12px;
				top:50%;
				margin:-5.5px 0 0;
				content:'';
			}
		}
	}
	.lst-popular {
		position: relative;
		min-height: calculateRem(40px);
		padding:9px 23px;
		counter-reset: popular;
		&:before {
			position: absolute;
			left:0;
			top:0;
			display: block;
			width: 100%;
			padding-top:18px;
			font-size: calculateRem(14px);
			text-align: center;
			color:#959595;
			content:'인기 검색어를 준비중입니다.';
		}
		li {
			position: relative;
			height: calculateRem(35px);
			line-height: calculateRem(35px);
			background-color:#fff;
		}
		a {
			display: block;
			font-size: calculateRem(14px);
			color:#333;
			overflow: hidden;
			&:before {
				display: inline-block;
				min-width:24px;
				margin-top: -3px;
				font-size: calculateRem(14px);
				color:#43c866;
				vertical-align: middle;
				counter-increment: popular;
				content: counter(popular);
			}
		}
	}
	.lst-autocomplete {
		position: relative;
		min-height: calc(100vh - 109px);
		padding:8px 23px;
		li {
			height: calculateRem(35px);
			line-height: calculateRem(35px);
			background-color: #fff;
			overflow:hidden;
			position: relative;
		}
		a {
			display: block;
			font-size: calculateRem(14px);
			color:#333;
			overflow: hidden;
			&:before {
				@include ico_srch_word();
				margin-right: 8px;
				margin-top: -2px;
				content:'';
				vertical-align: middle;
			}
		}

	}	
}