@charset "UTF-8";

.comn-footer {
	// max-width: 750px;
	margin:0 auto;
	padding:0;
	background-color:#444;
	font-size: calculateRem(12px);
	.f-social{
		height: 42px;
		line-height: 42px;
		text-align: center;
		border-bottom: 1px solid #666;
		a {
			display: inline-block;
			height: 42px;
			padding:0 12px;
			&:after {
				content:'';
				vertical-align: middle;
			}
			&.facebook {
				&:after {
					@include btn_foot_sns_facebook();
				}
			}
			&.kakaotalk {
				&:after {
					@include btn_foot_sns_kakaotalk();
				}
			}
			&.kakaostory {
				&:after {
					@include btn_foot_sns_kakaostory();
				}
			}
			&.blog {
				&:after {
					@include btn_foot_sns_blog();
				}
			}
			&.insta {
				&:after {
					@include btn_foot_sns_insta();
				}
			}
			&.post {
				&:after {
					@include btn_foot_sns_post();
				}
			}
		}
	}
	.f-contact {
		padding:9px 24px 11px;
		.call {
			text-align: center;
			margin:3px 0;
			a {
				display: block;
				height: calculateRem(21px);
				line-height: calculateRem(21px);
				color:#fff;
			}
		}
		.run {
			line-height: 1.55;
			color:rgba(255,255,255,.5);
			a {
				display: block;
				height: calculateRem(23px);
				line-height: calculateRem(23px);
				text-align: center;
				&:after {
					@include ico_foot_more();
					margin-left: 5px;
					content:'';
					vertical-align: middle;
				}
				
			}
			&.time {
				.part {
					margin:0 0 6px;
				}
				.hidden-area {
					margin:0 0 7px;
					padding:7px 0 3px;
					border-bottom: 1px solid #666;
				}
			}
			&.number {
				.part {
					margin:0 0 2px;
					overflow: hidden;
				}
				.num {
					float:left;
					width:10%;
				}
				.item {
					float:left;
					width:80%;
				}
			}
		}
	}
	.f-util {
		padding:14px 23px 100px;
		background-color: #3a3a3a;
		color:rgba(255,255,255,.5);
		.links {
			margin-top:-1px;
			font-size: 0;
			text-align: center;
			a {
				height: calculateRem(23px);
				line-height: calculateRem(23px);
				font-size: calculateRem(12px);
				&:before {
					display: inline-block;
					width:1px;
					height:1px;
					margin:3px 5px 5px;
					background:rgba(255,255,255,.5);
					content:'';
					vertical-align: middle;
				}
				&:first-child {
					&:before {
						content: none;
					}
				}
			}
			strong {
				font-weight: bolder;
			}
		}
		.groupware {
			padding:12px 24px 0;
			margin:0 0 12px;
			text-align: center;
			a {
				display: inline-block;
				height: calculateRem(30px);
				line-height: calculateRem(28px);
				padding:0 29px;
				border: 1px solid #666;
				border-radius:3px;
			}
		}
		.information {
			margin-top:3px;
			a {
				display: block;
				height: calculateRem(21px);
				line-height: calculateRem(21px);
				text-align: center;
				&:after {
					@include ico_foot_more();
					margin-left: 5px;
					content:'';
					vertical-align: middle;
				}
			}
			.hidden-area {
				padding:4px 0 5px;
				word-wrap: normal;
				> span {
					margin-right: 10px;
				}
				.entrepreneur {
					display: inline;
					text-decoration: underline;
					height: initial;
					line-height: initial;
					&:after {
						content:none;
					}
				}
			}
		}
		.copyright {
			height: calculateRem(21px);
			line-height: calculateRem(21px);
			font-size: calculateRem(11px);
			text-align: center;
		}
	}
	.hidden-area {
		display: none;
	}
	.btn-tog.on {
		&:after{
			transform: rotate(180deg);
		}
		& + .hidden-area {
			display: block;
		}
	}
}