body {
	min-width: 320px;
	letter-spacing: -0.02em;
	&.fixed-dim { //레어어팝업 내 스크롤 있을경우
		position: fixed;
		left: 0;
		top: 0;
		width:100%;
		height: 100%;
		overflow: hidden;
	}
	&.layer-dim { //레어어팝업 내 스크롤 없을경우
		position: absolute;
		left: 0;
		top: 0;
		width:100%;
		height: 100%;
		overflow: hidden;
	}
}
.blind {
	position:absolute;
	top:0;
	left:0;
	width:0;
	height:0;
	font-size:0;
	line-height:0;
	visibility:hidden;
}
.ir {
	display:block;
	width:100%;
	height:100%;
	visibility:hidden;
}
// 스크린 리더 전용 텍스트
.sr-only {
	position: absolute;
	left: -10000px;
	width: 1px;
	height: 1px;
	overflow: hidden;
}
//하이라이트 컬러투명처리
span,input,li,button,div,a {
	-webkit-tap-highlight-color:rgba(0,0,0,0);
}