.comn-page-title {
	position: relative;
	height:calculateRem(48px);
	line-height:calculateRem(47px);
	border-bottom:1px solid #e6e6e6;
	.comn-title {
		height: 100%;
		padding:0 55px 0 55px;
		font-size:calculateRem(16px);
		text-align: center;
		font-weight: normal;
		@include ellipsis();
	}
	.back {
		position: absolute;
		left:0;
		top:0;
		display: block;
		width:55px;
		height:calculateRem(48px);
		font-size:0;
		line-height:0;
		&:before {
			position: absolute;
			left: 24px;
			top: 50%;
			display: block;
			width: 9px;
			height: 1px;
			transform: rotate(-45deg);
			transform-origin: left;
			overflow: hidden;
			background: #333;
			content: '';
		}		
		&:after {
			position: absolute;
			left: 24px;
			top: 50%;
			display: block;
			width: 9px;
			height: 1px;
			transform: rotate(45deg);
			transform-origin: left;
			overflow: hidden;
			background: #333;
			content: '';
		}
	}
	.loading {
		position: absolute;
		left:0;
		top:calculateRem(47px);
		display: block;
		width: 100%;
		height: 1px;
		z-index: 1;
		background: linear-gradient(140deg, #43c866 0%, #43c866 45%, #2fb8c1 55%, #2fb8c1 100%);
	}
}
.btn-comtit-fav {
	position: absolute;
	right:40px;
	top:0;
	display: block;
	width:40px;
	height:calculateRem(48px);
	font-size:0;
	line-height:0;
	&:before {
		position: absolute;
		left:15px;
		top:50%;
		@include btn_comn_fav_off();
		margin:-7px 0 0 0;
		content:'';
	}
	&.is-on {
		&:before {
			@include btn_comn_fav_on();
		}
	}
}
.btn-comtit-share {
	position: absolute;
	right:0;
	top:0;
	display: block;
	width:40px;
	height:calculateRem(48px);
	font-size:0;
	line-height:0;
	&:before {
		position: absolute;
		right:23px;
		top:50%;
		@include btn_comn_share();
		margin:-7px 0 0 0;
		content:'';
	}
}

.pop-comtit-fav {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	transform-origin: center center;
	z-index: 1000;
	@include btn_comn_fav-ani();
	opacity: 0;
	animation: heartbeat 3s;
	display: none;
	@keyframes heartbeat {
		20% { transform: translate(-50%, -50%) scale( 1 ); opacity: 1;}
		24% { transform: translate(-50%, -50%) scale( .75 ); }
		28% { transform: translate(-50%, -50%) scale( 1 ); }
		32% { transform: translate(-50%, -50%) scale( .75 ); }
		36% { transform: translate(-50%, -50%) scale( 1 );}
		60% { opacity: 1;}
		100% { opacity: 0;}
	}
}
.pop-comtit-share {
	.pop-wrap {
		display: flex;
		height: 100%;
		justify-content: flex-end;
		flex-direction: column;
		.pop-con {
			padding:19px 24px 25px;
			position: relative;
			background-color:#fff;
	
		}
		.tit {
			margin:0 0 21px;
			font-weight:bold;
		}
		li + li {
			margin:15px 0 0;
		}
		a {
			display: block;
			font-size: 13px;
			text-decoration: none;
			color:#000;
			&:before {
				content: '';
				margin-right: 10px;
				  vertical-align: middle;
	
			}
			&.kakao {	
				&:before {
					@include ico_comn-pagetit-kakao();
				}
			}
			&.link {	
				&:before {
					@include ico_comn-pagetit-link();
				}
			}
			&.email {
				&:before {
					@include ico_comn-pagetit-email();
				}
			}
		}	
	}
}