@charset "UTF-8";

.top-banner {
	overflow: hidden;
	text-align: center;
	background-color:#3a3a3a;
	.banner-wrap {
		position: relative;
		max-width:750px;
		margin:0 auto;
	}
	a {
		display: block;
		// height: 47px;
		text-align:center;
		img {
			width: 100%;
			// height: 100%;
		}
	}
	.btn-close {
		position: absolute;
		left: 0;
		top:50%;
		width:47px;
		height:47px;
		margin-top:-23.5px;
		&:after {
			@include btn_comn_close();
			position: absolute;
			left:50%;
			top:50%;
			margin:-5.5px 0 0 -5.5px;
			content:'';
		}
	}
}
//공통헤더
.comn-header {
	max-width: 750px;
	margin:0 auto;
	position: relative;
	z-index: 99;
	// z-index: 106;
	height:51px;
	//아래는 구버전 겹치는 이슈로 재정의
	left:auto;
	top:auto;
	right:auto;
	padding: 0;
	background-color:transparent;
	.header-wrap {
		position: absolute;
		left:0;
		top:0;
		width:100%;
		height:51px;
		line-height:51px;
		background-color:transparent;
	}
	.logo-modetour {
		position: absolute;
		left:55px;
		top:0;
		display: block;
		width:83.5px;
		height: 100%;
		padding:0;
		a {
			position: relative;
			display: block;
			width: 100%;
			height: 100%;
			&:before {
				position: absolute;
				left:0;
				top:50%;
				display: inline-block;
				width:83.5px;
				height: 16.5px;
				margin-top:-8px;
				background: url("//img.modetour.com/ModeMobile/2019/common/logo.png") no-repeat 0 0;
				background-size: cover;
				content:'';
			}
		}
	}
	.link-tab {
		position: absolute;
		left:142px;
		top:0;
		height: 100%;
		overflow: hidden;
		li {
			position: relative;
			float:left;
			height: 100%;
			&:before {
				position: absolute;
				left:0;
				top:50%;
				display: block;
				width:1px;
				height:calculateRem(12.5px);
				margin-top:calculateRem(-6px);
				background:#fff;
				content:'';
			}
			&:first-child {
				&:before {
					content:none;
				}
			}
			a {
				display: block;
				padding:0 8px;
				color:#fff;
				font-size: calculateRem(14px);
			}
			@media only screen and (max-device-width:320px){
				a {
					padding:0 3px;
				}
			}
			&.tour {
				&.on {
					a {
						color:#ecf641;
						font-weight: bolder;
					}
				}
			}
			&.air {
				&.on {
					a {
						color:#25b4d8;
						font-weight: bolder;
					}
				}
			}
			&.hotel {
				&.on {
					a {
						color:#fac126;
						font-weight: bolder;
					}
				}
			}
			&.localtour {
				&.on {
					a {
						color:#ff9326;
						font-weight: bolder;
					}
				}
			}
			&.tourstory {
				a {
					padding:11px 0 10px;
					&:after {
						@include btn_comn_tour();
						content:'';
						vertical-align: middle;
					}

				}
			}
			&.bizmode {
				a {
					padding:10px 0 11px;
					&:after {
						@include btn_comn_biz();
						content:'';
						vertical-align: middle;
					}
				}
			}
			&.tourstory, &.bizmode {
				display: none;
			}
		}
	}
	.screen {
		position: fixed;
		left: 0;
		top: 0;
		display: none;
		width: 100%;
		height: 100%;
		overflow: hidden;
		z-index: 1;
		-webkit-tap-highlight-color:rgba(0,0,0,0);
	}
	&.fix {
		.header-wrap {
			position: fixed;
			left:0;
			top:0;
			width:100%;
			// height:51px;
			// line-height:51px;
			height:52px;
			line-height:52px;
			background-color:#fff;
			border-bottom:1px solid #e6e6e6;
			.link-tab { //fix상태
				li {
					display: none;
				}
				.tour {
					&.on {
						display: block;
						a {
							color:#43c866;
							&:after {
								@include btn_comn_gnb_tour();
								content:'';
								margin-top:-3px;
								margin-left:4px;
								vertical-align: middle;
							}
						}
					}
				}
				.air {
					&.on {
						display: block;
						a {
							color:#25b4d8;
							&:after {
								@include btn_comn_gnb_air();
								content:'';
								margin-top:-3px;
								margin-left:4px;
								vertical-align: middle;
							}
						}
					}
				}
				.hotel {
					&.on {
						display: block;
						a {
							color:#fac126;
							&:after {
								@include btn_comn_gnb_hotel();
								content:'';
								margin-top:-3px;
								margin-left:4px;
								vertical-align: middle;
							}
						}
					}
				}
				.localtour {
					&.on {
						display: block;
						a {
							color:#ff9326;
							&:after {
								@include btn_comn_gnb_localtour();
								content:'';
								margin-top:-3px;
								margin-left:4px;
								vertical-align: middle;
							}
						}
					}
				}
			}
			.link-tab.is-drop { //드롭박스 상태
				position: absolute;
				left:117.5px;
				top:6px;
				display: block;
				width: calculateRem(95px);
				height: auto;
				background:#fff;
				border:1px solid #e6e6e6;
				border-radius: 6px;
				z-index: 2;
				li {
					display: block;
					width: calc(100% - 10px);
					margin:0 5px;
					border-top:1px solid #e6e6e6;
					&:first-child {
						border-top:none;
					}
					&.on {
						a {
							&:after {
								content:none;
							}
						}
					}
					a {				
						display: block;
						line-height:16px;
						padding:10px 0;
						text-align: center;
						font-weight: bolder;
						letter-spacing: 0;
						font-size: calculateRem(14px);
					}
					&.tour {
						a {
							color:#43c866;
						}
					}
					&.air {
						a {
							color:#25b4d8;
						}
					}
					&.hotel {
						a {
							color:#fac126;
						}
					}
					&.localtour {
						a {
							color:#ff9326;
						}
					}
					&.tourstory {
						display: block;
						a {
							padding:11px 0;
						}
					}
					&.bizmode {
						display: block;
						a {
							padding:11.5px 0;
						}
					}
				}
					
				& + .screen {
					display: block;
				}
			}
			.comn-cate-open{
				&:after {					
					@include btn_comn_allmenu_fix();
				}
			}
		}
		&.is-scroll-down {
			.header-wrap{				
				top:-52px;
				border-bottom:none;
				box-shadow:none;
			}
		}
	}
}
.comn-cate-open {
	position: absolute;
	left:0;
	top:0;
	width:55px;
	height: 100%;
	padding:0;
	background:none;
	transition: all .5s;
	outline: none;
	border:none;
	&:after {
		@include btn_comn_allmenu();
		content:'';
		margin-left:3px;
		margin-top:3px;
	}
}
.categories {
	position: fixed;
	left: -100%;
	top: 0;
	width:100%;
	height:100%;
	z-index:100;
	// z-index:106;
	-webkit-overflow-scrolling: touch;
	transition: left .3s ease-in-out;
	&.is-open {
		left: 0;
		> .screen {
			display:block;
			animation: screenShow .5s ease-in-out;
		}
	}
	&:before {
		position: absolute;
		left:0;
		top:0;
		display: block;
		width:94%;
		max-width: 352px;
		height:100%;
		background-color: #fff;
		content:'';
	}
	> .screen {
		position: absolute;
		left: 0;
		top: 0;
		display:none;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: rgba(0,0,0,.7);
	}
	@keyframes screenShow {
		0% {opacity:0;}
		50% {opacity:0;}
		100% {opacity:1;}
	}
	.comn-cate-close {
		position: absolute;
		right:0;
		top:0;
		display: block;
		width:36px;
		height:60px;
		&:after {
			@include btn_comn_close();
			position: absolute;
			left:0;
			top:50%;
			margin:-6.5px 0 0 0;
			content:'';
		}
	}
	.comn-cate-set {
		position: absolute;
		right:50px;
		top:0;
		display: block;
		width:36px;
		height:60px;
		&:after {
			@include btn_comn_setting();
			position: absolute;
			right: 0;
			top:50%;
			margin:-9px 0 0 0;
			content:'';
		}
	}
}
.category-setting {
	background:#fafafa;
	.chk-wrap {
		float: right;
		input{ 
			opacity: 0;
			width: 0;
			height: 0;
		}
		input:checked + .switch {
			background-color: #43c866;
			&:before {
				left:28px;
			}
			&:after {
				right:auto;
				left: 2px;
				content:'ON';
			}
		}
		.switch {
			position: relative;
			display: inline-block;
			width: 50px;
			height:24px;
			border-radius: 12px;
			vertical-align: middle;
			background:#cfcfcf;
			transition: .4s;
			&:before {
				position: absolute;
				top: 2px;
				left: 2px;
				display: block;
				height: 20px;
				width: 20px;
				content: "";
				border-radius: 12px;
				background:#fff;
				transition: .2s;
			}
			&:after {
				position: absolute;
				top: 2px;
				right: 2px;
				display: block;
				height: 20px;
				line-height: 20px;
				width: 28px;
				text-align: center;
				content: "OFF";
				font-size: calculateRem(11px);
				color:#fff;
			}
		}
	}
	.pop-tit {
		height:49px;
		padding:0 58px;
		line-height:49px;
		text-align: center;
		background:#fff;
		border-bottom: 1px solid #e6e6e6;
		@include ellipsis;
		.head {
			display: inline-block;
			font-size:calculateRem(15px);
		}
		.close {
			position: absolute;
			right:0;
			top:0;
			display: block;
			width:16px;
			height:16px;
			padding:25px 26px;
			cursor: pointer;
			@include txtHidden;
			&:before {
				position: absolute;
				top:50%;
				left:50%;
				content:'';
				margin:0 0 0 -10px;
				width:15px;
				height:1px;
				background:#313333;;
				transform: rotate(-45deg);
			}
			&:after {
				position: absolute;
				top:50%;
				left:50%;
				content:'';
				margin:0 0 0 -10px;
				width:15px;
				height:1px;
				background:#313333;;
				transform: rotate(45deg);
			}
		}
	}
	.pop-con {
		display: block;
		width:100%;
		height: 100%;
		overflow: hidden;
		overflow-y:auto;
		-webkit-overflow-scrolling: touch;
		word-break:break-all;
		padding-bottom: 50px;
		.tit-box {
			position: relative;
			z-index:1;
			height: 43px;
			line-height: 43px;
			padding:0 24px;
			color:#9e9e9e;
			font-weight: normal;
			font-size:calculateRem(14px);
		}
		.set-box {
			border-bottom:1px solid #e6e6e6;
		}
		.lst-connect {
			overflow: hidden;
			li {
				position: relative;
				height: 47px;
				line-height: 47px;
				margin-top:-1px;
				padding:0 24px;
				background:#fff;
				color:#333;
				font-weight: bold;
				font-size: calculateRem(14px);
				overflow: hidden;
				&:after {
					position: absolute;
					left:24px;
					top:0;
					display:block;
					content:'';
					width:calc(100% - 48px);
					height:1px;
					background:#e6e6e6;
				}
			}
			a {
				display:block;
			}
		}
		.lst-txt {
			padding:20px 24px 18px;
			li {
				position: relative;
				margin:0 0 14px;
				padding-left:10px;
				color:#b3b3b3;
				font-size: calculateRem(13px);
				&:before {
					position: absolute;
					left:0;
					top:9px;
					display:block;
					width:3px;
					height:3px;
					content:'';
					border-radius: 100%;
					background:#b3b3b3;
				}
			}
		}
		.version {
			margin:36px 0 64px;
			text-align: center;
			.v-tit {
				margin: 0 0 15px;
				font-size: calculateRem(14px);
				color:#333;
			}
			.v-box {
				display: flex;
				width:60%;
				height: 41px;
				margin:0 auto;
				padding:10px 0 8px;
				border:1px solid #b3b3b3;
				border-radius: 2px;
			}
			.v-txt {
				position: relative;
				flex:auto;
				font-size: calculateRem(12px);
				color:#b3b3b3;
				&:first-child {
					&:after {
						position: absolute;
						right:0;
						top:0;
						display:block;
						width:1px;
						height:22px;
						content:'';
						background:#b3b3b3;
					}
				}
			}

		}
	}
}
