.category-search {
	position: relative;
	padding:5px 5px 5px;
	background-color:#fafafa;
	border-bottom:1px solid #e6e6e6;
	&:before {
		@include ico_comn_search();
		position: absolute;
		left:30px;
		top:50%;
		margin:-7px 0 0 -7px;
		content:'';
	}
	input, a {
		display: inline-block;
		width:100%;
		height:calculateRem(28px);
		line-height:calculateRem(26px);
		padding-left:38px;
		background-color:#fff;
		border:none;
		font-size:calculateRem(14px);
		border-radius:calculateRem(14px);
		color:#bebebe;
