.category-links {
	position: relative;
	background-color: #fafafa;
	overflow:hidden;	
	&:after {
		position: absolute;
		right:0;
		bottom:0;
		display: block;
		width:100%;
		height:1px;
		content:'';
		z-index: 1;
		background-color:#e6e6e6;
	}
	.tab-place {
		float:left;
		li {
			&:last-child {
				a {
					background-color:#f4f4f4;
					border-bottom:none;
					&.current {
						background-color:#fff;
					}
				}
			}
		}
		a {
			display: block;
			width:132px;
			height:62px;
			padding-left:20px;
			padding-right:10px;
			font-weight: bolder;
			font-size:calculateRem(15px);
			color:#959595;
			background-color:#fafafa;
			margin-top:-1px;
			border-top:1px solid #f0f0f0;
			border-bottom:1px solid #f0f0f0;
			&:after {
				display: inline-block;
				height:100%;
				content:'';
				vertical-align: middle;
			}
			&.current {
				width:132px;
				position: relative;
				color:#43c866;
				background-color:#fff;
				&:before {
					position: absolute;
					right:-1px;
					top:0;
					display: block;
					width:1px;
					height:100%;
					content:'';
					z-index: 1;
					background-color:#fff;
				}
			}
			
		}
		span {
			display: inline-block;
			width: 100%;
			vertical-align: middle;
			line-height: 1.3;
		}
	}
	.place-one {
		float:right;
		width:calc(100% - 132px);
		min-height: calculateRem(366px);
		padding:19px 16px 6px 21px;
		background-color:#fff;
		border-left: 1px solid #e6e6e6;
		color:#959595;
		font-size: calculateRem(15px);
	}
	.place-two { 
		> a {
			position: relative;
			display: block;
			padding:0 7px 0 0;
			margin:0 0 7px;
			-webkit-tap-highlight-color:rgba(0,0,0,.1);
			&:after {
				position: absolute;
				right:0;
				top:50%;
				margin-top:-2px;
				@include ico_comn_more();
				transform: rotate(180deg);
				content:'';
				vertical-align: middle;
			}
		}
		&.on {
			> a {
				color:#000;
				font-weight: bolder;
				&:after {
					transform: rotate(0deg);
				}
				~ div {
					display: block;
				}
			}
			.place-three:last-child { //선택된 메뉴의 하단여백
				margin-bottom:18px;
			}
		}
	}
	.place-three {
		&.on {
			.place-four:last-child { //선택된 메뉴의 하단여백
				margin-bottom:11px;
			}
		}
	}
	.place-three, .place-four, .place-five {
		display: none;
		margin:0 0 3px;
		padding: 0 0 0 15px;
		font-size: calculateRem(14px);
		> a {
			position: relative;
			display: block;
			margin:0 0 5px;
			padding:0 7px 0 0;
			&:after {
				position: absolute;
				right:0;
				top:50%;
				margin-top:-2px;
				@include ico_comn_more2();
				transform: rotate(180deg);
				content:'';
				vertical-align: middle;
			}
		}
		&.on {
			> a {
				color:#000;
				&:after {
					transform: rotate(0deg);
				}
				~ div {
					display: block;
				}
			}
		}
	}
	.place-two, .place-three, .place-four, .place-five { // 2depth, 3depth 하위메뉴 없을때, 화살표 삭제
		> a.nocate {
			&:after {
				color:inherit;
				content:none;
			}
		}
	}
	.airport { //부산지방
		display: grid;
		grid-template-columns: 33.3% 33.3% 33.3%;
		margin:24px -5px 20px -8px;
		a {
			display: block;
			min-height: 33px;
			line-height: 31px;
			text-align: center;
			margin:-1px 0 0 -1px;
			font-size: calculateRem(14px);
			border:1px solid #e6e6e6;
			&.current {
				position: relative;
				border-color: #43c866;
				color: #43c866;
			}
		}
	}
	.link-sub { //해외패키지, 자유여행, 테마여행, 국내여행
		position: relative;
		display: block;
		height: 42px;
		margin:0 0 18px;
		color:#333;
		font-weight: bold;
		border-bottom: 1px solid #e6e6e6;
		&:before {
			margin:-2px 5px 0 0 ;
			content:'';
			vertical-align: middle;
		}
		&:after {
			position: absolute;
			right:0;
			top:calculateRem(8px);
			content:'';
			vertical-align: middle;
			@include ico_comn_cate_arr();		
		}
	}
	#catePackage .link-sub {
		&:before {
			@include ico_comn_cate_pack();			
		}
	}
	#cateFree .link-sub {
		&:before {
			@include ico_comn_cate_indep();			
		}
	}
	#cateTheme .link-sub {
		&:before {
			@include ico_comn_cate_theme();			
		}
	}
	#cateDomestic .link-sub {
		&:before {
			@include ico_comn_cate_domestic();			
		}
	}
	#cateRegion .link-sub {
		&:before {
			@include ico_comn_cate_region();			
		}
	}
}
