$fontUrl : '//m.modetour.com/Include/Css/Fonts/NotoSansKr/';
@font-face {
	font-family: 'Noto Sans KR';
	font-style: normal;
	font-weight: 100;
	src: url($fontUrl + 'NotoSansKR-Thin.woff2') format('woff2'),
	url($fontUrl + 'NotoSansKR-Thin.woff') format('woff'),
	url($fontUrl + 'NotoSansKR-Thin.otf') format('opentype');
}
