@charset "UTF-8";

.float-btns-wrap {
	position:fixed;
	right:8px;
	bottom:25px;
	width:calculateRem(50px);
	z-index:3;
	overflow: hidden;
}
//맨 위로 이동(공통)
.comn-go-top {
	// display:none;
	margin-bottom: calculateRem(-50px);
	transition: margin-bottom .5s ease-in-out;
	&.is-show {
		margin-bottom:0;
	}
	a { 
		position: relative;
		display:block;
		width: calculateRem(43px);
		height: calculateRem(43px);
		margin-top: calculateRem(7px);
		margin-bottom: calculateRem(7px);
		border:1px solid #e7e7e7;
		background-color: rgba(255,255,255,.9);
		border-radius: 100%;
		overflow:hidden;
		box-shadow: 2px 2px 5px rgba(0,0,0,.15);
		box-sizing: border-box;
		&:after {
			@include btn_comn_top();
			position: absolute;
			left:50%;
			top:50%;
			margin:-6px 0 0 -6px;
			content:'';
		}
	}
}
//글자확대(공통)
.comn-font-change {
	a { 
		position: relative;
		display:block;
		width: calculateRem(43px);
		height: calculateRem(43px);
		margin-top: calculateRem(7px);
		border:1px solid #e7e7e7;
		background-color: rgba(255,255,255,.9);
		border-radius: 100%;
		overflow:hidden;
		box-shadow: 2px 2px 5px rgba(0,0,0,.15);
		box-sizing: border-box;
		&:after {
			@include btn_comn_font();
			position: absolute;
			left:50%;
			top:50%;
			margin:-8.5px 0 0 -8.5px;
			content:'';
		}
	}
}
//글자확대 팝업
.font-change-pop { //팝업 스크롤 없는 유형
	position: fixed;
	top: 50%;
	left: 50%;
	display: none;
	width: 100%;
	height: 100%;
	z-index: 1000;
	transform: translate3d(-50%, -50%, 0);
	.pop-wrap {
		position: absolute;
		left: 50%;
		top: 50%;
		display: block;
		width:85%;
		padding:37px 0 27px;
		border-radius: 2px;
		transform: translate(-50%, -50%);
		background-color: #fff;
	}
	.close {
		position: absolute;
		right: 0;
		top: 0;
		display: inline-block;
		width: 42px;
		height: 42px;
		padding:15px;
		font-size:calculateRem(14px);
		text-align: right;  
		z-index:1;
		&:after {
			@include btn_comn_close4();
			content:'';
			vertical-align: top;
		}
	}
	.txt-font {
		margin:0 0 20px;
		text-align: center;
		font-size: calculateRem(15px);
		color:#333;
	}
	.lst-calcuate {
		position: relative;
		width:100%;
		display: flex;
		li {
			flex:1;
			justify-content: space-around;
			&:last-child {
				a {
					&:before {
						content:none;
					}
				}
			}
		}
		a {
			position: relative;
			display: block;
			padding-top:12px;
			text-align: center;
			font-size: 13px;
			color:#a8a8a8;
			&:before {
				position: absolute;
				left:50%;
				top:5px;
				display: block;
				width:100%;
				height:1px;
				background-color:#a8a8a8;
				content:'';
			}
			&:after {
				position: absolute;
				left:50%;
				top:2px;
				display: inline-block;
				width:1px;
				height:7px;
				content:'';
				background-color: #a8a8a8;
			}
			&.current {
				&:after {
					position: absolute;
					left:50%;
					top:0;
					display: inline-block;
					width:10px;
					height:10px;
					margin-left:-5px;
					content:'';
					border-radius: 100%;
					background-color: #42c867;
				}
			}
		}
	}
}